code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --mainColor: #ff512f;
}

*:focus {
  outline: none;
}

.landingPage {
  color: var(--mainColor);
  background-color: #ffffffe3;
  padding: 10px 30px;
  border: 2px solid #ff512f;
  text-align: center;
  border-radius: 15px;
  width: 60%;
  /* max-width: 700px; */
}

.landingPage h2 {
  font-size: 4vw;
  text-align: center;
}

.landingPage p {
  text-align: center;
  font-size: 2vw;
  padding: 10px 30px;
}

.usersName {
  text-align: center;
}
input {
  outline: none;
  border: 1px solid #ff512f;
  color: #4e4e4e;
  margin: 20px 15px;
  padding: 5px 20px;
  background: aliceblue;
  border-radius: 20px;
}
.Input {
  width: 60%;
  font-size: 2.3vw;
}
.Social {
  font-size: 1.8vw;
  width: 55%;
  /* max-width: 300px; */
  margin-bottom: 20px;
}
#userSocialMedia p {
  text-align: left;
  font-size: 2.4vw;
  margin-bottom: 10px;
}
.SocialInputs {
  display: flex;
  width: 100%;
  height: 40px;
  justify-content: center;
  align-items: center;
  text-align: center;
}
#goButton {
  margin: 20px 0;
}
button {
  cursor: pointer;
  background: #ff512f;
  color: white;
  outline: none;
  padding: 7px 20px;
  border-radius: 15px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  margin-top: 20px;
}
.error {
  font-weight: 200;
  font-size: 25px;
  -webkit-animation: popUp 0.5s linear alternate;
          animation: popUp 0.5s linear alternate;
  -webkit-animation-iteration-count: 3;
          animation-iteration-count: 3;
  text-transform: capitalize;
}
@-webkit-keyframes popUp {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes popUp {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@media (max-width: 800px) {
  .landingPage {
    padding: 10px 10px;
    width: 80%;
  }
}
@media (max-width: 600px) {
  .landingPage {
    padding: 10px 10px;
    width: 90%;
  }
  .landingPage p {
    margin-bottom: 10px;
  }
  #userSocialMedia p {
    text-align: center;
  }
  input {
    margin: 0;
  }
  .SocialInputs {
    flex-direction: column;
    justify-content: flex-end;
    justify-items: flex-end;
    align-content: flex-end;
    justify-self: flex-end;
    align-self: flex-end;
    align-items: center;
    height: auto;
  }

  .Input {
    margin: auto;
    margin-bottom: 10px;
    width: 80%;
    /* font-size: 20px; */
  }
  .Social {
    /* font-size: 18px; */
    width: 70%;
    /* max-width: 300px; */
    margin-bottom: 20px;
  }
  button {
    margin: 0;
  }
}



@media (max-width:499px){
 .landingPage{
  width: 90vw;
  height: 70vh;;
 }
}
